<template>
    <div>
      <div class="">
        <b-row>
          <b-col lg="12">
          </b-col>
          <b-col lg="2" class="border-right">
            <h4 class="mb-1 text-right">{{ $route.fullPath.split("/")[1] }}</h4>
            <div class="vertical-menu shadow mt-4">
              <router-link v-for="x in submenu" :key="x.id" :to="x.path">{{ x.label }}</router-link>
            </div>
          </b-col>
          <b-col lg="10" class="mb-3">
            <router-view></router-view>
          </b-col>
        </b-row>
      </div>
    </div>
  </template>
  <script>
  import menu from '@/menu';
  export default {
    name: 'VerLayout',
    components: {
    },
  
    watch: { 
    '$route.fullPath': {
      handler: function(search) {
        //console.log(search)
        //alert("change route fullPath");
  
        let routes = JSON.parse(JSON.stringify(this.$store.state.routes));
        menu.routes = routes;
        let path = `/${this.$route.fullPath.split("/")[1]}`;
        this.submenu = menu.getChild(path);
  
  
      },
      deep: true,
      immediate: true
    }
  },
  
    computed: {
      ds2: function () {
        return [1, 2, 3];
      }
    },
    data() {
      return {
        ds: [],
        submenu: []
      }
    },
    created() {
    },
    mounted() {
      this.$nextTick(() => {
        let routes = JSON.parse(JSON.stringify(this.$store.state.routes));
        menu.routes = routes;
        let path = `/${this.$route.fullPath.split("/")[1]}`;
        this.submenu = menu.getChild(path);
      })
    },
    methods: {
    }
  }
  </script>
  <style scoped>
  .vertical-menu {
    width: 100%;
    /* Set a width if you like */
    background-color: whitesmoke;
    /*whitesmoke;*/
    border-left: 2px solid silver;
  }
  
  .vertical-menu a {
    background-color: white;
    /* Grey background color */
    color: black;
    /* Black text color */
    display: block;
    /* Make the links appear below each other */
    padding: 4px;
    /* Add some padding */
    text-decoration: none;
    /* Remove underline from links */
    border-bottom: 1px solid #dee2e6;
  }
  
  .vertical-menu a:hover {
    background-color: #ccc;
    /* Dark grey background on mouse-over */
  }
  
  .vertical-menu a.active {
    background-color: #4CAF50;
    /* Add a green color to the "active/current" link */
    color: white;
  }
  
  a.router-link-exact-active {
    font-weight: bold;
    background-color: greenyellow;
  }
  </style>
  